import { Navbar, NavItem, Icon } from 'react-materialize';

export default function Navigation() {
    return (
        <Navbar
            alignLinks="right"
            options={{
                draggable: true,
                edge: 'left',
                inDuration: 250,
                onCloseEnd: null,
                onCloseStart: null,
                onOpenEnd: null,
                onOpenStart: null,
                outDuration: 200,
                preventScrolling: true
            }}
        >
            <NavItem href="">
                Getting started
  </NavItem>
            <NavItem href="components.html">
                Components
  </NavItem>
        </Navbar>
    )
}